import axios from "axios";
import React from "react";
import SearchInput from "./searchInput";
import Image_list from "./image_list";
import { getByDisplayValue } from "@testing-library/react";
class App extends React.Component{
state = {images:[]}
constructor(){
    super();
    this.onSerachSubmit = this.onSerachSubmit.bind(this);
}
async onSerachSubmit (entry){
console.log(entry)
// `` using to enter the dynamic data into string
const response = await axios.get(`https://pixabay.com/api/?key=26489438-8eea53bd468d6abf7c84260ff&q=${entry}&image_type=photo&pretty=true`);
console.log(response.data.hits)
this.setState({images:response.data.hits})
} 
render(){
    return(
        <div className="container" >this is from app.js
            <SearchInput onSerachSubmit = {this.onSerachSubmit} /> 
<span>found {this.state.images.length}</span>

            <Image_list images = {this.state.images}/>
            </div>
    )
}
}
export default App;