import react from 'react';
import reactdom from 'react-dom';


//map function is used to render list .
const Image_list =(props)=>{
const images = props.images.map((image)=>{

    return (
     
    <img key={image.id} style={{width:'300px',margin:'50px'}} src ={image.webformatURL}  alt=''/>
    )
})
        return(
            <div className='container-fluid'><span style={{width:"200px"}} >{images}</span></div>
        )
    }


export default Image_list;